@import "https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --tenne-tawny: #d65108;
  --tenne-tawny-dark: #b54507;
}

body {
  font-family: Poppins, sans-serif;
  font-size: 1.05rem;
  font-weight: 300;
  line-height: 1.6;
}

.btn {
  cursor: pointer;
  outline: 0;
  font-family: inherit;
  font-size: 1.05rem;
}

.text {
  opacity: .8;
}

.title {
  margin-bottom: 1rem;
  font-size: 2rem;
}

.container {
  min-height: 100vh;
}

.meal-wrapper {
  max-width: 1280px;
  text-align: center;
  background: #fff;
  margin: 0 auto;
  padding: 2rem;
}

.meal-search {
  margin: 2rem 0;
}

.meal-search cite {
  font-size: 1rem;
}

.meal-search-box {
  align-items: stretch;
  margin: 1.2rem 0;
  display: flex;
}

.search-control, .search-btn {
  width: 100%;
}

.search-control {
  border: 1px solid var(--tenne-tawny);
  color: var(--tenne-tawny);
  border-top-left-radius: 2rem;
  border-bottom-left-radius: 2rem;
  outline: 0;
  padding: 0 1rem;
  font-family: inherit;
  font-size: 1.1rem;
}

.search-control::placeholder {
  color: var(--tenne-tawny);
}

.search-btn {
  width: 55px;
  height: 55px;
  background: var(--tenne-tawny);
  color: #fff;
  border: none;
  border-top-right-radius: 2rem;
  border-bottom-right-radius: 2rem;
  font-size: 1.8rem;
  transition: all .4s linear;
}

.search-btn:hover {
  background: var(--tenne-tawny-dark);
}

.meal-result {
  margin-top: 4rem;
}

#meal {
  margin: 2.4rem 0;
}

.meal-item {
  border-radius: 1rem;
  margin: 2rem 0;
  overflow: hidden;
  box-shadow: 0 4px 21px -12px #000000c9;
}

.meal-img img {
  width: 100%;
  display: block;
}

.meal-name {
  padding: 1.5rem .5rem 0;
}

.meal-name h3 {
  font-size: 1.4rem;
}

.recipe-btn, .favorite-btn, .removeFavorite-btn {
  color: #fff;
  background: var(--tenne-tawny);
  width: 175px;
  cursor: pointer;
  border: none;
  border-radius: 2rem;
  margin: 1rem auto;
  padding: .75rem 0;
  font-size: 1.1rem;
  font-weight: 500;
  text-decoration: none;
  transition: all .4s linear;
  display: block;
}

.removeFavorite-btn {
  border: 2px solid var(--tenne-tawny);
  color: var(--tenne-tawny);
  background: #fff;
}

.recipe-btn:hover {
  background: var(--tenne-tawny-dark);
}

.meal-buttons {
  display: flex;
}

.meal-details {
  z-index: 9999;
  color: #000;
  width: 90%;
  height: 90%;
  background: #fff;
  border-radius: 1rem;
  padding: 2rem 0;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -50%);
}

.meal-details::-webkit-scrollbar {
  width: 10px;
}

.meal-details::-webkit-scrollbar-thumb {
  -ms-border-radius: 2rem;
  -o-border-radius: 2rem;
  background: #f0f0f0;
  border-radius: 2rem;
}

.showRecipe {
  display: block;
}

.meal-details-content {
  margin: 2rem;
}

.meal-details-content p:not(.recipe-category) {
  padding: 1rem 0;
}

.recipe-close-btn {
  width: 35px;
  height: 35px;
  opacity: .9;
  background: #fff;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  display: flex;
  position: absolute;
  top: 2rem;
  right: 2rem;
}

.recipe-title {
  letter-spacing: 1px;
  padding-bottom: 1rem;
}

.recipe-category {
  color: var(--tenne-tawny);
  color: var(--tenne-tawny);
  background: #fff;
  border-radius: .3rem;
  padding: .2rem .5rem;
  font-weight: 600;
  display: inline-block;
}

.recipe-instruct {
  padding: 1rem 0;
}

.recipe-meal-img img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 0 auto;
  display: block;
}

.recipe-link {
  margin: 1.4rem 0;
}

.recipe-link a {
  color: var(--tenne-tawny-dark);
  font-size: 1.2rem;
  font-weight: 700;
  transition: all .4s linear;
}

.recipe-link a:hover {
  opacity: .8;
}

.notFound {
  color: var(--tenne-tawny);
  width: 100%;
  font-size: 1.8rem;
  font-weight: 600;
  grid-template-columns: 1fr !important;
}

@media screen and (min-width: 600px) {
  .meal-search-box {
    width: 540px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  #meal {
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    display: grid;
  }

  .meal-item {
    margin: 0;
  }

  .meal-details {
    width: 700px;
  }
}

@media screen and (min-width: 992px) {
  #meal {
    grid-template-columns: repeat(3, 1fr);
  }
}

